<template>
    <div class="container">
        <h2>drawer 侧弹框组件</h2>
        <h3>侧弹框组件</h3>
        <Pcode :code="code">
        </Pcode>

        <h3>属性</h3>
        <PAttrTable :data="attrs"/>

        <h3>事件</h3>
        <PEventTable :data="events"/>
    </div>
</template>

<script>
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import Pcode from '@/components/Pcode.vue'

export default {
    name: 'button',
    components: {
        PAttrTable, PEventTable, Pcode
    },
    data () {
        return {
            attrs: [
                {
                    param: 'show',
                    desc: '展示或隐藏',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                },
                {
                    param: 'mask',
                    desc: '是否需要遮罩',
                    type: 'Boolean',
                    optional: '-',
                    default: 'true'
                },
                {
                    param: 'width',
                    desc: '侧弹框宽度',
                    type: 'String',
                    optional: '-',
                    default: '230px'
                },
                {
                    param: 'right',
                    desc: '是否从右侧滑出',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                }
            ],
            events: [
                {
                    name: 'onClose',
                    desc: `组件关闭的时候触发`,
                    param: `-`
                }
            ],
            code: `
<script>
    import Taro, { Component } from '@tarojs/taro'
    import { View } from '@tarojs/components'
    import {
        WiDrawer,
        WiList,
        WiListItem,
    } from 'hosjoy-wi-ui'

    export default class Index extends Component {
        constructor(props){
            super(props)
            this.state = {
                show:false
            }
        }

        onCloseTest(){
            console.log('关闭后执行')
        }

        onShow(){
            this.setState({
                show:true
            })
        }

        render () {
          const { show } =  this.state
          return (
              <View className='index'>
                  <View onClick={()=>this.onShow()}>弹出</View>
                  <WiDrawer
                      show={show}
                      onClose={this.onCloseTest.bind(this)}
                  >
                      <WiList>
                          <WiListItem title="菜单1" />
                          <WiListItem title="菜单2" />
                      </WiList>
                  </WiDrawer>
              </View>
          )
        }
    }
<\/script>`
        }
    },
}
</script>

<style lang="scss" scoped>
.container {
    padding-bottom: 50px;
}
</style>
